import * as Scroll from 'react-scroll';

const toggleView = (e) => {
    const elem = ".accordion";

    if (!e.target.matches(elem + ' .a-btn')) return;

    if (!e.target.parentElement.classList.contains('active')) {
        
        // remove all actives 
        document
            .querySelectorAll(elem + ' .a-container')
            .forEach((e) => {
                e.classList.remove('active');
            });

        // place active
        e.target.parentElement.classList.add('active');
    } else {
        //remove active class on cliked accordion     
        e.target.parentElement.classList.remove('active');
    }


    const id = e.target.parentElement.id;
    const scroller = Scroll.scroller;
    scroller.scrollTo(id, {
        smooth: false,
        offset: -60,
    });
}

export const Accordion = (props) => {

    return <div className="accordion">
        {props.children}
    </div>
}

export const AccordionTab = (props) => {
    const Element = Scroll.Element;

    return <Element name={props.id} id={props.id} className="a-container">
        <p className="a-btn" onClick={toggleView}>&nbsp;&nbsp;&nbsp;{props.title}<span></span></p>
        <div className="a-panel">{props.children}</div>
    </Element>
}