import { useState } from 'react';
import { Link } from "react-router-dom";

const Nav = (props) => {

    const [toggle, setToggle] = useState(false);

    const generateLink = (id, title) => {
        return <li
            className={`item ${toggle ? "active" : ""}`}
            onClick={() => setToggle(false)}
        >
            <Link to={{ pathname: "/", hash: `#${id}` }}>{title}</Link>
        </li>
    }

    return (
        <nav>
            <div style={{ display: "flex", justifyContent: "center" }}>
                <div style={{ width: 900 }}>
                    <ul className="menu">
                        <li className="logo" onClick={() => setToggle(false)}>
                            <Link to="/">El Búho Tuerto</Link>
                        </li>
                        { generateLink("news", "Noticias") }
                        { generateLink("awards", "Premios") }
                        { generateLink("restaurant", "Restaurante")}
                        { generateLink("about", "Sobre nosotros")}      
                        <li
                            className={`item button ${toggle ? "active" : ""}`}
                            onClick={() => setToggle(false)}
                        >
                            <Link to="/menu">Menú</Link>
                        </li>
                        <li className="toggle" onClick={(e) => {
                            setToggle(!toggle);
                        }}>
                            <span className="bars"></span>
                        </li>
                    </ul>
                </div>
            </div>
        </nav>)
}

export default Nav;
