import Page from '../components/Page'
import SlideShow from '../components/SlideShow';

const images = [
    "2016.jpg",
    "2017.jpg",
    "2018.jpg",
    "2019.jpg",
    "choice-2020.gif",
    "choice-2021.gif",
    "guru-2020.jpg",
    "guru-2021.jpeg",
];

const AwardsPage = () => {
    return <Page title="Premios" id="awards">
        <ul className="container">
            {images.map(it => {
                return <li key={it}>
                    <img
                        className="item"
                        src={`${process.env.PUBLIC_URL}/assets/images/awards/${it}`}
                        alt={`${it}`}
                    />
                </li>
            })}
        </ul>

        <SlideShow />
    </Page>
}

export default AwardsPage;