import * as Scroll from 'react-scroll';

const Page = (props) => {
    const Element  = Scroll.Element;
    return <Element className="page" id={props.id}>
        <div className="main">
            {props.title && <h1 className="title">{props.title}</h1>}
            {props.children}
        </div>
    </Element>
}

export default Page;