import axios from "axios";
import { useEffect, useState } from "react";
import Page from "../components/Page";
import { Link, useLocation } from "react-router-dom";
import { Accordion, AccordionTab } from "../components/Accordion";

const languages = ["es", "de", "ar", "en"];


const getDefaultLanguage = () => {
    const browserLanguage = window.navigator.userLanguage || window.navigator.language;
    if (!browserLanguage) {
        return "es";
    }
    const bl = browserLanguage.toLowerCase();

    if (bl === "es-ar" || bl === "es-uy") {
        return "ar";
    }

    if (bl.startsWith("es")) {
        return "es";
    }

    if (bl.startsWith("de")) {
        return "de";
    }

    return "en";
}


const MenuPage = (props) => {

    const [menu, setMenu] = useState({});
    const [selectedLanguage, setSelectedLanguage] = useState("es");

    const search = useLocation().search;
    const language = new URLSearchParams(search).get('language');

    useEffect(() => {

        // select language
        let lng = !language ? getDefaultLanguage() : language.toLocaleLowerCase();
        if (!languages.includes(lng)) {
            lng = "es";
        }

        setSelectedLanguage(lng);

        axios
            .get(`${process.env.PUBLIC_URL}/assets/i18n/i18n.${lng}.json`)
            .then(reponse => {
                setMenu(reponse.data)
            });
    }, [language]);

    const createFood = (food, index) => {
        return <div className="food-container" key={food.title + "-" + index}>
            <div className="food-description">
                <div className="food-title">{food.title}</div>
                <div className="food-subtitle" dangerouslySetInnerHTML={{ __html: food.subtitle }} />
                {food.allergies && food.allergies.length > 0 && <div className="food-allergies">
                    {food.allergies.sort().map((it, index2) => {
                        return <img
                            key={index + "-" + index2}
                            width="25"
                            height="25"
                            src={`${process.env.PUBLIC_URL}/assets/images/menu/allergies/${it}.jpg`}
                            alt={it}
                        />
                    })}
                </div>
                }
            </div>
            <div className="food-info">
                <div className="food-price">{food.price}</div>
                {food.special && <div className="food-special">{food.special}</div>}
            </div>
        </div>
    }

    return <Page id="menu" title="">

        <div className="language">
            {languages.map((language, index) => {
                return <Link
                    key={language + "-" + index}
                    to={{
                        pathname: "/menu",
                        search: `?language=${language}`
                    }}
                >
                    <img
                        className={selectedLanguage === language ? "active" : ""}
                        src={`${process.env.PUBLIC_URL}/assets/images/flags/${language.replace("en", "gb")}.svg`}
                        alt={language}
                    />
                </Link>
            })}
        </div>

        <Accordion>
            {menu?.menu?.map((category, index) => {
                return <AccordionTab id={`${index}`} title={category.title} key={category.title}>
                    {category.foods.map((food, index) => createFood(food, index))}
                </AccordionTab>
            })}

            <AccordionTab id={`puntos`} title="Puntos de la Carne">
                {
                    menu["meat-image"] && <img className="image" width="100%" src={`${process.env.PUBLIC_URL}${menu["meat-image"]}`} alt="Meat" />
                }
                <div>
                    {menu?.cooking?.map(it => {
                        return <div key={it.title}>
                            <div className="carne-title">{it.title}</div>
                            <div className="carne-subtitle">{it.subtitle}</div>
                        </div>
                    })}
                </div>
            </AccordionTab>
        </Accordion>

        <div className="tripadvistor">
            <a
                target="_blank"
                rel="noreferrer"
                href="https://www.tripadvisor.es/Restaurant_Review-g187472-d7729499-Reviews-El_Buho_Tuerto-Las_Palmas_de_Gran_Canaria_Gran_Canaria_Canary_Islands.html"
            >
                <img
                    src="https://static.tacdn.com/img2/brand_refresh/Tripadvisor_lockup_horizontal_secondary_registered.svg"
                    alt="TripAdvisor"
                />

                <p>
                    Haz click aquí danos tu opiníon
                </p>
            </a>
            
        </div>
    </Page>
}

export default MenuPage;