import { useEffect, useState } from "react";


const SlideShow = (props) => {

    const [selectedIndex, setSelectedIndex] = useState(1);

    const moveSlide = (n) => {

        const newIndex = selectedIndex + n;

        if (newIndex > props.images.length) { 
            setSelectedIndex(1);
            return;
        }

        if (newIndex < 1) {
            setSelectedIndex(props.images.length);
            return;
        }

        setSelectedIndex(newIndex);
    }

    const createImage = (index, length, text) => {
        return <div key={`img-${index}`} className="mySlides fade">
            <div className="numbertext">{`${index + 1} / ${length}`}</div>
            <img className="image" src={`${process.env.PUBLIC_URL}${props.images[index]}`} alt={text} />
            <div className="text">{text}</div>
        </div>
    }   

    useEffect(() => {

        const slides = document.getElementsByClassName("mySlides");
        const dots = document.getElementsByClassName("dot");
        
        if(slides.length === 0) {
            return;
        }

        for (let i = 0; i < slides.length; i++) {
            slides[i].style.display = "none";
        }

        for (let i = 0; i < dots.length; i++) {
            dots[i].className = dots[i].className.replace(" active", "");
        }

        slides[selectedIndex - 1].style.display = "block";
        dots[selectedIndex - 1].className += " active";

    }, [selectedIndex]);


    return <div className="slideshow">
        <div className="slideshow-container">
            {props.images.map((it, index) => {
                return createImage(index, props.images.length, "")
            })}

            <div className="prev" onClick={() => moveSlide(-1)}>&#10094;</div>
            <div className="next" onClick={() => moveSlide(1)}>&#10095;</div>
        </div>

        <br />

        <div style={{ textAlign: "center" }}>
            {props.images.map((it, index) => {
                return <span key={`dot-${index}`} className="dot"></span>
            })}
        </div>
    </div>
}

export default SlideShow;

SlideShow.defaultProps = {
    images: [
        "/assets/images/gallery/segunda-portada-bandera.jpg",
        "/assets/images/gallery/IMG_5575.JPG",
        "/assets/images/gallery/IMG_5579.JPG",
        "/assets/images/gallery/IMG_5580.JPG",
        "/assets/images/gallery/IMG_5707.JPG",
        "/assets/images/gallery/IMG_5737.JPG",
        "/assets/images/gallery/IMG_5798.JPG",
        "/assets/images/gallery/IMG_5812.JPG",
        "/assets/images/gallery/IMG_6009.JPG",
        "/assets/images/gallery/2021-10-16-01.23.11.jpeg",
        "/assets/images/gallery/2021-10-16-01.24.03.jpeg",
        "/assets/images/gallery/2021-10-16-01.24.22.jpeg",
    ]
}